<template>
  <el-container>
    <el-header>
      <div style="width: 100%;height: 2rem;"></div>
      <el-row>
        <el-col :span="4"></el-col>
        <el-col :span="8">
          <el-page-header title="首页" @back="goBack" content="Build Name 搜索页">
          </el-page-header>
        </el-col>
        <el-col :span="4"></el-col>
      </el-row>
    </el-header>
    <el-row :gutter="20">

      <el-col :span="4">
        <el-aside>
        </el-aside>
      </el-col>
      <el-col :span="16">
        <el-main>
          <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="Build Name ">
              <el-input v-model="form.buildname"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">搜索</el-button>
            </el-form-item>
          </el-form>
        </el-main>

        <showContent v-show="datas!=''" :datas="datas"></showContent>

      </el-col>

    </el-row>
  </el-container>

</template>

<script>
import ajax_obj from "@/utils/ajax/api";
import showContent from "@/components/showContent";
  export default {
    components:{
      showContent,
    },
    data() {
      return {
        form: {
          buildname: ""
        },
        datas:""
      }
    },
    methods: {
      goBack() {
        location.href = "/"
      },
      onSubmit() {
        const data = ajax_obj.search_by_build_name(this.form.buildname)
        data.then((response) => {
          this.datas = eval(response['data']['data'])
          console.log(this.datas)
        })
      }
    }
  }
</script>

