import { createRouter, createWebHistory } from 'vue-router'
import IndexView from '../views/Index.vue'
import SBName from '../views/SearchByName.vue'
import SBBname from '../views/SearchByBuildName.vue'
import SBSId from '../views/SearchBySessionId.vue'

const routes = [
  {
    path: '/',
    name: 'index',
    component: IndexView
  },
  {
    path: '/search/name',
    name: 'name',
    component: SBName
  },
  {
    path:'/search/buildname',
    component:SBBname
    
  },
  {
    path :'/search/sessionid',
    component:SBSId
  }
]

const router = createRouter({
  // history: createWebHistory(process.env.BASE_URL),
  history: createWebHistory(window.location.path),

  routes
})

export default router
