</template>

<script>
import overviewshow from "@/components/overviewshow";
export default {
  components: {
    overviewshow
  },
  methods: {
    handleSelect(key, keyPath) {
      if (keyPath[0] == 2) {
        location.href = "/search/name"
      }
      if (keyPath[0] == 3) {
        location.href = "/search/buildname"
      }
      if (keyPath[0] == 4) {
        location.href = "/search/sessionid"
      }
    }
  }
};
</script>

