<template>
  <el-card class="box-card">
    <template #header>
    </template>
    <div v-for="data in datas" :key="data" class="text item">
      name : {{ data['name'] }}
      <br>
      status_ind :
      <el-button v-show="data['status_ind']==='running'" type="primary" size="mini"
                 @click="openvideo_url(data['video_url'])">{{ data['status_ind'] }}
      </el-button>
      <el-button v-show="data['status_ind']==='completed'" type="success" size="mini" disabled>{{data['status_ind'] }}
      </el-button>

      <br>
      duration : {{ data['duration'] }}
      <br>
      build_name : <el-link :underline="false" :href ="search_by_build_name(data['build_name'])">{{data['build_name']}}</el-link>
      <br>
      session_id : {{ data['session_id'] }}
      <el-divider></el-divider>
    </div>
  </el-card>
</template>

<script>
export default {
  name: "showContent",
  props: ["datas"],
  methods: {
    openvideo_url(video_url) {
      window.open(video_url, '_blank').location;
    },
    search_by_build_name(build_name) {
      return '/search_by_build_name/' + build_name
    },
    delete_by_session_id(session_id){
      return '/delete_by_session_id/' + session_id
    }
  }
}
</script>

