<template>
  <!--  刷新按钮开始-->
  <el-switch
      v-model="freshvalue"
      active-color="#13ce66"
      inactive-color="#ff4949">
  </el-switch>
  <!--  刷新按钮结束-->
<!--  表格开始-->
  <el-table
      :data="tableData"
      style="width: 100%">
    <el-table-column type="expand">
      <template #default="props">
        <el-form label-position="left" inline class="demo-table-expand">
          <el-form-item label="build_name : ">
            <span>{{ props.row.build_name }}</span>
          </el-form-item>
          <el-form-item label="status_ind : ">
            <span>{{ props.row.status_ind }}</span>
          </el-form-item>
          <el-form-item label="session_id : ">
            <span>{{ props.row.session_id }}</span>
          </el-form-item>
          <el-form-item label="video_url : ">


            <el-link :underline="false" :href="props.row.video_url">video url</el-link>

<!--            <span>{{ props.row.video_url }}</span>-->
          </el-form-item>
          <el-form-item label="duration : ">
            <span>{{ props.row.duration }}</span>
          </el-form-item>
          <el-form-item label="ltime : ">
            <span>{{ props.row.ltime }}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column
        label="ltime"
        prop="ltime">
    </el-table-column>
    <el-table-column
        label="name"
        prop="name">
    </el-table-column>
    <el-table-column
        label="build_name"
        prop="build_name">
    </el-table-column>
    <el-table-column
        label="status_ind"
        prop="status_ind">
    </el-table-column>
  </el-table>
  <!--  表格结束-->
</template>

<script>
import ajax_obj from "@/utils/ajax/api";

export default {
  name: "overviewshow",
  data() {
    return {
      tableData: "",
      timer: "",
      freshvalue: true
    }
  },
  methods: {
    getOverView() {
      if (this.freshvalue == true) {
        ajax_obj.get_over_view().then((response) => {
          if (response.status === 200) {
            var newData = eval(response.data.data)
            if (newData !== this.tableData) {
              this.tableData = newData
            }
          }
        })
      }
    }
  },
  computed: {
  },
  mounted() {
    this.timer = setInterval(this.getOverView, 3000);
  },
  beforeUnmount() {
    clearInterval(this.timer);
  }
}
</script>

<style scoped>
.demo-table-expand {
  font-size: 0;
}

.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}

.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>